<template><undefined>
<h1>Upload 上传</h1>
<p>文件选择上传和拖拽上传控件。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>上传是将信息（网页、文字、图片、视频等）通过网页或者上传工具发布到远程服务器上的过程。</p>
<ul>
<li>当需要上传一个或一些文件时。</li>
<li>当需要展现上传的进度时。</li>
<li>当需要使用拖拽交互时。</li>
</ul>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-upload :name=\&quot;name\&quot; :action=\&quot;action\&quot; @change=\&quot;onChange\&quot;&gt;\n    &lt;v-button type=\&quot;ghost\&quot;&gt;\n      &lt;v-icon type=\&quot;upload\&quot;&gt;&lt;/v-icon&gt;点击上传\n    &lt;/v-button&gt;\n  &lt;/v-upload&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data: ()=&gt; ({\n      name: 'file',\n      action: '/upload'\n    }),\n    methods: {\n      onChange(info) {\n        if (info.file.status !== 'uploading') {\n          console.log(info.file, info.fileList)\n        }\n        if (info.file.status === 'done') {\n          console.log(info.file.name + ' 上传成功.')\n        } else if (info.file.status === 'error') {\n          console.log(info.file.name + ' 上传失败.')\n        }\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-upload :name="name" :action="action" @change="onChange">
    <v-button type="ghost">
      <v-icon type="upload"></v-icon>点击上传
    </v-button>
  </v-upload>
</template>


</div>
                  <div slot="description"><h4 id="dian-ji-shang-chuan"><a class="header-anchor" href="#dian-ji-shang-chuan" aria-hidden="true">¶</a> 点击上传</h4>
<p>经典款式，用户点击按钮弹出文件选择框。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-upload</span> <span class="hljs-attr">:name</span>=<span class="hljs-string">"name"</span> <span class="hljs-attr">:action</span>=<span class="hljs-string">"action"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"onChange"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"ghost"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">v-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"upload"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-icon</span>&gt;</span>点击上传
    <span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">v-upload</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
      <span class="hljs-attr">name</span>: <span class="hljs-string">'file'</span>,
      <span class="hljs-attr">action</span>: <span class="hljs-string">'/upload'</span>
    }),
    <span class="hljs-attr">methods</span>: {
      onChange(info) {
        <span class="hljs-keyword">if</span> (info.file.status !== <span class="hljs-string">'uploading'</span>) {
          <span class="hljs-built_in">console</span>.log(info.file, info.fileList)
        }
        <span class="hljs-keyword">if</span> (info.file.status === <span class="hljs-string">'done'</span>) {
          <span class="hljs-built_in">console</span>.log(info.file.name + <span class="hljs-string">' 上传成功.'</span>)
        } <span class="hljs-keyword">else</span> <span class="hljs-keyword">if</span> (info.file.status === <span class="hljs-string">'error'</span>) {
          <span class="hljs-built_in">console</span>.log(info.file.name + <span class="hljs-string">' 上传失败.'</span>)
        }
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
