<tbody>
<tr>
<td>name</td>
<td>可选参数, 上传的文件</td>
<td>string</td>
<td>file</td>
</tr>
<tr>
<td>action</td>
<td>必选参数, 上传的地址</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>fileList</td>
<td>默认已经上传的文件列表</td>
<td>object[]</td>
<td>[]</td>
</tr>
<tr>
<td>data</td>
<td>可选参数, 上传所需参数</td>
<td>object</td>
<td>{}</td>
</tr>
<tr>
<td>header</td>
<td>可选参数, 设置request头信息</td>
<td>object</td>
<td>{}</td>
</tr>
<tr>
<td>beforeUpload</td>
<td>上传文件之前的钩子，参数为上传的文件，若返回 false 或者 Promise 则停止上传。注意：该方法不支持老 IE</td>
<td>(file, reqOptions) =&gt; boolean</td>
<td>Promise</td>
</tr>
<tr>
<td>multiple</td>
<td>可选参数, 是否支持多选文件</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>accept</td>
<td>可选参数, 接受上传的文件类型, 详见 input accept Attribute</td>
<td>string</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="upload-events"><a class="header-anchor" href="#upload-events" aria-hidden="true">¶</a> Upload Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>可选参数, 上传文件改变时的状态，详见 onChange</td>
<td>fileInfo</td>
</tr>
</tbody>
</table>
<h3 id="onchange"><a class="header-anchor" href="#onchange" aria-hidden="true">¶</a> onChange</h3>
<p>文件状态改变的回调，返回为：</p>
<pre v-pre=""><code v-pre="" class="hljs">{
  file: { ... },
  fileList: [ ... ],
  event: { ... }
}
</code></pre>
<p>1.file 当前操作的文件对象。</p>
<pre v-pre=""><code v-pre="" class="hljs">{
  uid: 'uid',      // 文件唯一标识，建议设置为负数，防止和内部产生的 id 冲突
  name: 'xx.png'   // 文件名
  status: 'done',  // 状态有：uploading done error removed
  response: '{&quot;status&quot;:&quot;success&quot;}'  // 服务端响应内容
}
</code></pre>
<p>如果上传控件是 multiple 时，此参数将为一个对象数组 [file, ...]。<br>
2.fileList 当前的文件列表。<br>
3.event 上传中的服务端响应内容，包含了上传进度等信息，高级浏览器支持。</p>
<h2 id="xian-shi-xia-zai-lian-jie"><a class="header-anchor" href="#xian-shi-xia-zai-lian-jie" aria-hidden="true">¶</a> 显示下载链接</h2>
<p>请使用 fileList 属性设置数组项的 url 属性进行展示控制。</p>
</undefined></template>

<script>
  export default {
    data: ()=> ({
      name: 'file',
      action: '/upload',
      header:{
        appkey: 'FDF8F7DSFSD8FSA987A8SF',
      },
      defaultFileList: [{
        uid: -1,
        name: 'xxx.png',
        status: 'done',
        url: 'http://www.baidu.com/xxx.png'
      }, {
        uid: -2,
        name: 'yyy.png',
        status: 'done',
        url: 'http://www.baidu.com/yyy.png'
      }]
    }),
    methods: {
      onChange(info) {
        if (info.file.status !== 'uploading') {
          console.log(info.file, info.fileList)
        }
        if (info.file.status === 'done') {
          console.log(info.file.name + ' 上传成功.')
        } else if (info.file.status === 'error') {
          console.log(info.file.name + ' 上传失败.')
        }
      }
    }
  }
</script>


